body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: rgba(219, 217, 217, 0.945);
  color: rgb(7, 7, 7);
  overflow-x: hidden;
  width: 100vw;
}
.container {
  width: 95%;
  margin-left: 50%;
  transform: translateX(-50%);
  margin-top: 10vh;
  background-color: rgba(219, 217, 217, 0.945);
  box-shadow: 1px 2px 5px 4px rgba(0, 0, 0, 0.37);
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  transition: 1.5sec ease-in;
}
.Header {
  font-size: 9px;
  color: rgb(10, 10, 10);
  position: absolute;
  top: 20px;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.balance {
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 1px;
  margin-top: 70px;
  margin-bottom: 40px;
}
.balanceRed {
  font-weight: 900;
  font-size: 15px;
  color: rgb(189, 8, 8);
}
.balanceGreen {
  font-weight: 900;
  font-size: 15px;
  color: rgb(5, 175, 90);
}
.incomeExpense {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 12px;
  background-color: rgba(219, 217, 217, 0.945);
  padding: 5px;
  box-shadow: 1px 2px 2px 1px rgba(0, 0, 0, 0.445);
  width: 90%;
}
.incomeExpense .Income {
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
}
.incomeExpense .expense {
  display: flex;
  flex-direction: column;

  text-transform: uppercase;
  letter-spacing: 1px;
}
.incomeExpense .Income span {
  color: rgb(5, 175, 90);
  margin-top: 10px;
  right: 10px;
}

.incomeExpense .expense span {
  color: rgb(189, 8, 8);
  margin-top: 10px;
  right: 10px;
}
.incomeExpenseDivider {
  height: 20px;
  margin-top: 6px;
  width: 1.5px;
  background-color: rgb(107, 4, 0);
  margin-right: 60px;
  margin-left: 60px;
}
.TransactionHistory {
  margin-top: 20px;
  width: 100%;
  margin-left: 30px;
}
.TransactionHistory h3 {
  font-weight: 600;
}
.transactionList li {
  width: 100%;
  background-color: rgb(224, 224, 224);
  display: flex;
  padding: 7px;
  box-shadow: 1px 2px 2px 1px rgba(0, 0, 0, 0.445);
  font-weight: 600;
  font-size: 13px;
  margin-bottom: 10px;
  list-style: none;
  margin-left: -40px;
}
.transactionList span {
  position: absolute;
  right: 20px;
}
.transactionColor {
  height: 32px;
  width: 5px;
  background-color: rgb(8, 189, 104);
  right: 10px;
  position: absolute;
  margin-top: -7px;
}
.transactionColorExpense {
  height: 32px;
  width: 5px;
  background-color: rgb(189, 8, 8);
  right: 10px;
  position: absolute;
  margin-top: -7px;
}
.addTransaction {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.addTransaction h3 {
  font-size: 14px;
  margin: 10px;
  font-weight: 600;
  border-bottom: 2px solid rgb(8, 189, 104);
  padding-bottom: 3px;
  padding-left: 5px;
}
.addTransaction label {
  font-weight: 600;
  margin-left: 10px;
  padding-bottom: 3px;
  padding-left: 5px;
  font-size: 14px;
  margin-bottom: 3px;
  display: flex;
  flex-direction: column;
}
.addTransaction form input {
  background-color: rgba(240, 240, 240, 0.507);
  border: 0px;
  box-shadow: 1px 2px 2px 1px rgba(0, 0, 0, 0.445);
  font-weight: 500;
  letter-spacing: 1px;
  font-size: 13px;
  padding: 8px;
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
  margin-left: 15px;
  justify-content: center;
  align-items: center;
  width: 88%;
}
.addTransaction input:focus {
  outline: none;
}
.addTransaction button {
  border: 0px;
  padding: 10px;
  width: 100%;
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  font-weight: 600;
  width: 90%;
  margin-left: 50%;
  transform: translateX(-50%);
  box-shadow: 1px 2px 2px 1px rgba(0, 0, 0, 0.445);
  margin-bottom: 10px;
  letter-spacing: 0.5px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  margin-bottom: 40px;
}
.addTransaction button:hover {
  background-color: rgb(173, 173, 173);
  width: 92%;
  cursor: pointer;
  color: black;
}
.addTransaction h4 {
  position: absolute;
  font-size: 10px;
  left: 80px;
  margin-top: -23px;
  color: rgb(105, 105, 105);
  font-weight: 700;
}
.deleteTransaction {
  color: rgb(250, 250, 250);
  opacity: 0;
  cursor: pointer;
  position: absolute;
  right: 30px;
  margin-top: -2px;
  background-color: rgb(139, 4, 4);
  padding: 1px 5px;
  transition: 0.3s ease;
  transition: linear;
  box-shadow: 1px 2px 2px 1px black;
}
.transactionList li:hover .deleteTransaction {
  opacity: 1;
  margin-left: 20px;
}
.transactionList li:hover .deleteTransaction {
  opacity: 1;
  margin-left: 20px;
}
.transactionList li:hover span {
  display: none;
}
.Cover {
  width: 100vw;
}
.Cover img {
  max-width: 75px;
  margin-top: 10px;
  margin-left: 50%;
  transform: translateX(-50%);
  transition: 0.4sec ease-in;
}
.Cover h1 {
  position: absolute;
  font-size: 10px;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-left: 50%;
  transform: translateX(-50%);
  top: 65px;
  color: rgb(255, 255, 255);
  text-shadow: 1px 1px rgba(29, 28, 27, 0.267);
}
.footer {
  background-color: rgb(7, 7, 7);
  color: rgb(255, 255, 255);
  display: flex;
  align-items: center;
  height: 30px;
  justify-content: center;
  position: absolute;
  bottom: 0;
  width: 100vw;
}
.burger img {
  max-width: 30px;
  position: absolute;
  top: 17px;
  right: 20px;
}
@media only screen and (min-width: 800px) {
  .container {
    width: 450px;
    margin-left: 50%;
  }
  .addTransaction form input {
    width: 90%;
  }
  .addTransaction h4 {
    margin-top: -21px;
  }

  .Cover img {
    height: auto;
    width: 100vw;
  }
  .micheal img {
    height: 100px;
    width: 140px;
    position: absolute;
    top: 0px;
    right: 130px;
  }
}
